.header {
  position: fixed; /* Fixed position to stay on top */
  top: 0;
  left: 0;
  width: 100%;
  background-color: black;
  padding: 10px 0;
  text-align: center;
  border-bottom: 2px solid white; /* White stroke at the bottom */
  z-index: 10; /* Higher z-index to stay above the snow */
}

.menu-item {
  color: white;
  text-decoration: none;
  margin: 0 15px;
  font-family: "Helvetica", sans-serif;
  font-weight: bold;
}

.menu-item:hover {
  text-decoration: underline; /* Style for hover state */
}
